import {makeAutoObservable} from "mobx";

export default class AudioStore {
    constructor() {
        this._types = []
        this._artists = []
        this._audios = []
        this._selectedType = {}
        this._selectedArtist = {}
        this._page = 1
        this._totalCount = 0
        this._limit = 3
        makeAutoObservable(this)
    }

    setTypes(types) {
        this._types = types
    }
    setArtists(artists) {
        this._artists = artists
    }
    setAudios(audios) {
        this._audios = audios
    }

    setSelectedType(type) {
        this.setPage(1)
        this._selectedType = type
    }
    setSelectedArtist(artist) {
        this.setPage(1)
        this._selectedArtist = artist
    }
    setPage(page) {
        this._page = page
    }
    setTotalCount(count) {
        this._totalCount = count
    }

    get types() {
        return this._types
    }
    get artists() {
        return this._artists
    }
    get audios() {
        return this._audios
    }
    get selectedType() {
        return this._selectedType
    }
    get selectedArtist() {
        return this._selectedArtist
    }
    get totalCount() {
        return this._totalCount
    }
    get page() {
        return this._page
    }
    get limit() {
        return this._limit
    }
}
