import React, {useContext, useEffect} from 'react';
import {Container} from "react-bootstrap";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
import TypeBar from "../components/TypeBar";
import ArtistBar from "../components/ArtistBar";
import AudioList from "../components/AudioList";
import {observer} from "mobx-react-lite";
import {Context} from "../index";
import {fetchArtists, fetchAudios, fetchTypes} from "../http/audioAPI";
import Pages from "../components/Pages";

const Shop = observer(() => {
    const {audio} = useContext(Context)

    useEffect(() => {
        fetchTypes().then(data => audio.setTypes(data))
        fetchArtists().then(data => audio.setArtists(data))
        fetchAudios(null, null, 1, 2).then(data => {
            audio.setAudios(data.rows)
            audio.setTotalCount(data.count)
        })
    }, [])

    useEffect(() => {
        fetchAudios(audio.selectedType.id, audio.selectedArtist.id, audio.page, 2).then(data => {
            audio.setAudios(data.rows)
            audio.setTotalCount(data.count)
        })
    }, [audio.page, audio.selectedType, audio.selectedArtist,])

    return (
        <Container>
            <Row className="mt-2">
                <Col md={3}>
                    <TypeBar/>
                </Col>
                <Col md={9}>
                    <ArtistBar/>
                    <AudioList/>
                    <Pages/>
                </Col>
            </Row>
        </Container>
    );
});

export default Shop;
