import {$authHost, $host} from "./index";
import jwt_decode from "jwt-decode";

export const createType = async (type) => {
    const {data} = await $authHost.post('api/type', type)
    return data
}

export const fetchTypes = async () => {
    const {data} = await $host.get('api/type')
    return data
}

export const createArtist = async (artist) => {
    const {data} = await $authHost.post('api/artist', artist)
    return data
}

export const fetchArtists = async () => {
    const {data} = await $host.get('api/artist', )
    return data
}

export const createAudio = async (audio) => {
    const {data} = await $authHost.post('api/audio', audio)
    return data
}

export const fetchAudios = async (typeId, artistId, page, limit= 5) => {
    const {data} = await $host.get('api/audio', {params: {
            typeId, artistId, page, limit
        }})
    return data
}

export const fetchOneAudio = async (id) => {
    const {data} = await $host.get('api/audio/' + id)
    return data
}
