import React, {useState} from 'react';
import {Button, Container} from "react-bootstrap";
import CreateArtist from "../components/modals/CreateArtist";
import CreateAudio from "../components/modals/CreateAudio";
import CreateType from "../components/modals/CreateType";

const Admin = () => {
    const [artistVisible, setArtistVisible] = useState(false)
    const [typeVisible, setTypeVisible] = useState(false)
    const [audioVisible, setAudioVisible] = useState(false)

    return (
        <Container className="d-flex flex-column">
            <Button
                variant={"outline-dark"}
                className="mt-4 p-2"
                onClick={() => setTypeVisible(true)}
            >
                Добавить тип
            </Button>
            <Button
                variant={"outline-dark"}
                className="mt-4 p-2"
                onClick={() => setArtistVisible(true)}
            >
                Добавить бренд
            </Button>
            <Button
                variant={"outline-dark"}
                className="mt-4 p-2"
                onClick={() => setAudioVisible(true)}
            >
                Добавить устройство
            </Button>
            <CreateArtist show={artistVisible} onHide={() => setArtistVisible(false)}/>
            <CreateAudio show={audioVisible} onHide={() => setAudioVisible(false)}/>
            <CreateType show={typeVisible} onHide={() => setTypeVisible(false)}/>
        </Container>
    );
};

export default Admin;
